<template>
  <div>
    <da-manage-page>
      <div slot="button">
        <el-button icon="my-zengjia" @click="dialogVisible=true;">新增</el-button>
      </div>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="对象类型">
          <!-- <el-input v-model="searchForm.formdata.type"></el-input> -->
          <el-select v-model="searchForm.formdata.type" filterable size="medium" placeholder="请选择">
            <el-option v-for="(item, index) in objectTypeData" :key="index" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="用能对象">
          <el-input v-model="searchForm.formdata.objectId"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
          <template slot-scope="scope">
            <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
            <el-button size="small" @click="delTb(scope.row)">删除</el-button>
          </template>
        </el-table-column>

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
      <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">

        <el-form-item label="对象类型" prop="type">
          <!-- <el-input auto-complete="off" v-model="addForm.type"></el-input> -->
          <el-select auto-complete="off" v-model="addForm.type" filterable size="medium" @change="changeType" placeholder="请选择">
            <el-option v-for="(item, index) in objectTypeData" :key="index" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="用能对象" prop="objectId">
          <!-- <el-input auto-complete="off" v-model="addForm.objectId"></el-input> -->
          <el-select auto-complete="off" v-model="addForm.objectId" filterable size="medium" @change="changeObject" placeholder="请选择">
            <el-option v-for="(item, index) in useObjectData" :key="index" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="数据项" prop="meterItemId">
          <!-- <el-input auto-complete="off" v-model="addForm.meterItemId"></el-input> -->
          <el-select auto-complete="off" v-model="addForm.meterItemId" filterable size="medium" placeholder="请选择">
            <el-option v-for="(item, index) in objectMeterItemData" :key="index" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="待机阈值" prop="standbyThreshold">
          <el-input auto-complete="off" v-model="addForm.standbyThreshold"></el-input>
        </el-form-item>
        <el-form-item label="停机阈值" prop="stopThreshold">
          <el-input auto-complete="off" v-model="addForm.stopThreshold"></el-input>
        </el-form-item>
      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
export default {
  name: "threshold",
  data() {
    return {
      objectTypeData: [],
      useObjectData: [],
      objectMeterItemData: [],
      // 查询表单
      searchForm: {
        formdata: { type: "", objectId: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头

        { label: "对象类型", prop: "type", align: "left", sortable: true },
        { label: "用能对象", prop: "objectId", align: "left", sortable: true },
        { label: "数据项", prop: "meterItemId", align: "left", sortable: true },
        {
          label: "待机阈值",
          prop: "standbyThreshold",
          align: "left",
          sortable: true
        },
        {
          label: "停机阈值",
          prop: "stopThreshold",
          align: "left",
          sortable: true
        }
      ],
      dialogVisible: false,
      addForm: {
        type: "",
        objectId: "",
        meterItemId: "",
        standbyThreshold: "",
        stopThreshold: ""
      },
      rules: this.eachValidate({
        type: { required: true, maxString: "" },
        objectId: { required: true, maxString: "" },
        meterItemId: { required: true, maxString: "" },
        standbyThreshold: { maxString: "" },
        stopThreshold: { maxString: "" }
      })
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑运行状态参数配置" : "新增运行状态参数配置";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getUseEnergyObjectType() {
      this.$ajax.get("rest/threshold/objectType").then(res => {
        if (res.data.status) {
          this.objectTypeData = res.data.records;
          //   let toObj = {};
          //   this.$each(res.data.records, item => {
          //     toObj[item.id] = item.name;
          //   });
          //   this.colsData[0].displace = toObj;
        }
      });
    },
    getUseEnergyObject() {
      this.$ajax
        .get("rest/threshold/object", {
          params: { objectType: this.addForm.type }
        })
        .then(res => {
          if (res.data.status) {
            this.useObjectData = res.data.records;
            //   let toObj = {};
            //   this.$each(res.data.records, item => {
            //     toObj[item.id] = item.name;
            //   });
            //   this.colsData[0].displace = toObj;
          }
        });
    },
    getUseObjectMeterItem() {
      this.$ajax
        .get("rest/threshold/meterItem", {
          params: {
            objectType: this.addForm.type,
            objectId: this.addForm.objectId
          }
        })
        .then(res => {
          if (res.data.status) {
            this.objectMeterItemData = res.data.records;
            //   let toObj = {};
            //   this.$each(res.data.records, item => {
            //     toObj[item.id] = item.name;
            //   });
            //   this.colsData[0].displace = toObj;
          }
        });
    },
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/threshold/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName].table
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    editTb(row) {
      this.dialogVisible = true;
      this.addForm = JSON.parse(JSON.stringify(row));
    },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {
          type: "",
          objectId: "",
          meterItemId: "",
          standbyThreshold: "",
          stopThreshold: ""
        };
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/threshold", this.addForm).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.dialogVisible = false;
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    },
    delTb(row) {
      this.$confirm("是否删除该记录?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$ajax
          .delete("rest/threshold", { params: { id: row.id } })
          .then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
      });
    },
    // 联动
    changeType() {
      this.getUseEnergyObject();
    },
    changeObject() {
      this.getUseObjectMeterItem();
    }
  },
  mounted() {
    this.getUseEnergyObjectType();
    this.getUseEnergyObject();
    this.getUseObjectMeterItem();
    this.getTbList();
  }
};
</script>



